.my-account-header {
    padding: 20px 50px;
    background-color: #f7f7f7;
    border-top: 1px solid #ececec;
    border-bottom: 1px solid #ececec;
}

@media screen and (min-width:768px) {
    .my-account-container {
        padding: 30px 15px;
        margin: 0px 90px;
    }
}

.profile-name {
    margin-left: 10px;
    font-size: x-large;

}

.profile-section {
    margin: 15px 0px;
    display: flex;
    align-items: center;
}

.side-menu-section {
    margin-right: 60px;
}

.outlet-section {
    margin-top: 25px;
}

.order-btn {
    background-color: black;
    color: white;
    font-size: large;
    font-weight: 600;
    padding: 10px;
}

.text-style {
    color: #777777;
    font-size: larger;
    font-weight: 600;
}

.wishlist-heading {
    border-bottom: 4px solid #c5c5c5;
    margin: 0px 30px;
    padding: 5px 0px;
}

.edit-account-container .error {
    width: 100%;
    margin-top: 0.25rem;
    font-size: .875em;
    color: #dc3545;
}

.edit-account-container .errorInputField {
    border-color: #dc3545;
    padding-right: calc(1.5em + 0.75rem);
    background-image: url("data:image/svg+xml,%3csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 12 12%27 width=%2712%27 height=%2712%27 fill=%27none%27 stroke=%27%23dc3545%27%3e%3ccircle cx=%276%27 cy=%276%27 r=%274.5%27/%3e%3cpath stroke-linejoin=%27round%27 d=%27M5.8 3.6h.4L6 6.5z%27/%3e%3ccircle cx=%276%27 cy=%278.2%27 r=%27.6%27 fill=%27%23dc3545%27 stroke=%27none%27/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: right calc(0.375em + 0.1875rem) center;
    background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    display: none;
}

.edit-account-container .errorSelectField {
    border-color: #dc3545;
    padding-right: calc(1.5em + 0.75rem);
    background-image: url("data:image/svg+xml,%3csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 12 12%27 width=%2712%27 height=%2712%27 fill=%27none%27 stroke=%27%23dc3545%27%3e%3ccircle cx=%276%27 cy=%276%27 r=%274.5%27/%3e%3cpath stroke-linejoin=%27round%27 d=%27M5.8 3.6h.4L6 6.5z%27/%3e%3ccircle cx=%276%27 cy=%278.2%27 r=%27.6%27 fill=%27%23dc3545%27 stroke=%27none%27/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: right calc(0.375em + 0.1875rem) center;
    background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}

.text-bold-md {
    font-weight: 400;
}

.single-order-container .first-section {
    display: flex;
    justify-content: space-between;
}

.single-order-container .third-section .price-details {
    display: flex;
    flex-direction: column;
}

.single-order-container .first-section .order-status {
    text-align: center;
}

@media screen and (max-width:576px) {

    .wishlist-heading {
        display: none;
    }

    .order-msg {
        display: block;
    }

    .myAccountTitle {
        font-size: 28px;
    }

    .single-order-container .first-section {
        flex-direction: column;
        align-items: flex-start;
    }

    .single-order-container .first-section .order-status {
        margin-top: 1rem;
    }
}